<script>
import BaseContainer from '../assets/BaseContainer.vue'
import SectionContainer from '../assets/SectionContainer.vue'



export default {
  components: { BaseContainer, SectionContainer },
  data() {
    return {
      oldWorks: [
        {
          projectName: "Let's Do it",
          topic: 'Global web page',
          filterName: 'global',
          imgSrc: require('../../public/lets-do-it.png'),
          imgAlt: "Let's Do it",
          description:
            "To-do web site, from this site you can crteate your own account and start add your to-to lists, you have choices to edit task name or delete task, also you can check on task to mark it as done task\n HTML5,CSS3,pure JS,JSON",
        },
        {
          projectName: 'one-page personal portfolio',
          topic: 'personal page',
          filterName: 'personal',
          imgSrc: require('../../public/s-p-p-1.png'),
          imgAlt: 'Social page template',
          description:
            "a one-page portfolio paage, using this page you can show your portfolio to more pepole arround the world\n HTML5,CSS3,pure JS,JSON",
        },
        {
          projectName: 'Social page',
          topic: 'Social page',
          filterName: 'social',
          imgSrc: require('../../public/social-page.png'),
          imgAlt: 'Social page template',
          description:
            ' Lorem ipsum dolor sit amet consectetur, adipisicing elit. Eveniet amet, neque itaque placeat blanditiis veniam accusamus asperiores saepe dolore est doloribus excepturi porro quod error nobis fuga? Ducimus, molestias error!\n HTML5,CSS3,pure JS,JSON',
        },
        {
          projectName: 'commercial one-page',
          topic: 'commercial page',
          filterName: 'global',
          imgSrc: require('../../public/commercial-one-page.png'),
          imgAlt: 'Social page template',
          description:
            ' Lorem ipsum dolor sit amet consectetur, adipisicing elit. Eveniet amet, neque itaque placeat blanditiis veniam accusamus asperiores saepe dolore est doloribus excepturi porro quod error nobis fuga? Ducimus, molestias error!',
        },
        {
          projectName: 'one-page personal portfolio',
          topic: 'personal page',
          filterName: 'personal',
          imgSrc: require('../../public/one-page-portfolio.png'),
          imgAlt: 'Social page template',
          description:
            "a one-page portfolio paage, using this page you can show your portfolio to more pepole arround the world\n HTML5,CSS3,pure JS,JSON",
        },
        {
          projectName: 'one-page personal photography portfolio',
          topic: 'personal page',
          filterName: 'personal',
          imgSrc: require('../../public/photography-one-page.png'),
          imgAlt: 'personal page',
          description:
            "a one-page portfolio paage, using this page you can show your portfolio to more pepole arround the world\n Vue.js,JSON",
        },
        {
          projectName: 'Book Shop Web Site',
          topic: 'commercial',
          filterName: 'global',
          imgSrc: require('../../public/bookShop2.png'),
          imgAlt: 'Social page template',
          description:
            "multiple-pages Book shop site, using this site you can show your products to more pepole arround the world\n HTML5,CSS3,Typescript,JSON",
        },
      ],
    }
  },
  mounted() {
    let btns = document.querySelectorAll('#myBtnContainer span')
    // console.log(btns)
    btns.forEach((btn) => {
      btn.addEventListener('click', function () {
        // console.log(btn)
        // remove class active fromm last active
        var current = document.querySelectorAll('#myBtnContainer .active')
        current[0].classList.remove('active')

        // get all elements will be filtered
        let allElm = document.querySelectorAll('#project-cards .filterDiv')

        // check type of sellected btn
        if (btn.getAttribute('data-tp') === 'all') {
          // add show class to all elements
          allElm.forEach((elm) => {
            elm.classList.add('show')
          })
        } else {
          // show class will be added to current type of filter
          allElm.forEach((elm) => {
            let s = btn.getAttribute('data-tp')
            if (s !== null) {
              if (elm.classList.contains(s)) {
                elm.classList.add('show')
              } else {
                elm.classList.remove('show')
              }
            }

          })
        }
        // add class active to current btn
        btn.classList.add('active')
      })
    })
  }
}
</script>
<style scoped lang="scss">
$main-color: #41b883;
$main-alt-color: #41b88337;

.filter {
  margin-bottom: 30px;

  span {
    padding: 5px;

    &:hover {
      border-bottom: 2px solid #000;
      color: $main_color;
      cursor: pointer;
    }

    &.active {
      color: $main_color;
    }
  }
}

.my-works {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  margin-left: 20px;
  margin-right: 20px;
  overflow: hidden;
  margin-bottom: 100px;

  @media (max-width: 567px) {
    grid-template-columns: minmax(200px, 1fr);
    gap: 10px;
  }

  .card {
    // background-color: $main-alt-color;
    padding: 5px;
    border: 1px dotted $main-color;
    border-radius: 10px;
    display: none;

    &.show {
      display: block;
    }

    img {
      width: 100%;
      border-radius: 10px;
      opacity: 0.7;
      transition: opacity 0.3s;
      cursor: pointer;
    }

    &:hover img {
      opacity: 1;
    }

    h3 {
      margin-top: 15px;
      margin-bottom: 15px;
      text-transform: uppercase;
      font-weight: bold;
    }

    span {
      display: block;
      padding-left: 15px;
      text-align: left;
      text-transform: capitalize;
      font-size: 10px;
      color: #2d2d2d;
      line-height: 20px;
    }

    p {
      display: block;
      text-align: left;
      padding-left: 15px;
    }

    .offer {
      text-decoration: none;
      color: $main-color;
    }
  }
}
</style>
