<template>
  <section-container>
    <h2 class="main-title">Work With me</h2>
    <!-- <h4>Will be happy to help you</h4> -->
    <form @submit.prevent="sendForm">
      <div class="control">
        <label for="name">Name</label>
        <input :class="{ invalid: !name.isValid }" type="text" id="name" placeholder="Type your name"
          v-model.trim="name.val" />
        <p v-if="!name.isValid">Please type your name</p>
      </div>

      <div class="control">
        <label for="email">E-mail</label>
        <input :class="{ invalid: !email.isValid }" type="email" id="email" placeholder="Type your email"
          v-model.trim="email.val" />
        <p v-if="!email.isValid">Please type your email</p>
      </div>

      <div class="control">
        <label for="phone">Phone number</label>
        <input :class="{ invalid: !phone.isValid }" type="phone" id="phone"
          placeholder="Type your phone started with 00" v-model.number="phone.val" />
        <p v-if="!phone.isValid">Please type your phone</p>
      </div>

      <div class="control">
        <label for="subject">Subject</label>
        <input :class="{ invalid: !subject.isValid }" type="text" id="subject" placeholder="Type a subject"
          v-model.trim="subject.val" />
        <p v-if="!subject.isValid">Please type a subject</p>
      </div>

      <div class="control">
        <label for="description">Description</label>
        <textarea :class="{ invalid: !description.isValid }" id="description" rows="5" placeholder="Type your message"
          v-model.trim="description.val"></textarea>
        <p v-if="!description.isValid">Please type your message</p>
      </div>

      <!-- <div class="control">
        <label for="budget">Budget</label>
        <select name="budget" id="" v-model="budget">
          <option value="_10">$10 - $100</option>
          <option value="_100" selected>$100 - $1000</option>
          <option value="_1000">$1000 - $2000</option>
          <option value="_2000">$2000 - $5000</option>
          <option value="_5000">$5000 - $10000</option>
        </select>
      </div> -->
      <!-- <div class="control">
        <label for="lunchDate">Target Lunch</label>
        <input type="date" id="lunchDate" v-model="lunchDate" />
      </div> -->
      <div class="control">
        <button type="submit">Send Message</button>
      </div>
      <p v-if="sentSucces" class="sent">Your message sent succesfuly</p>
    </form>
  </section-container>
</template>

<script>
export default {
  data() {
    return {
      formIsValid: true,
      name: {
        val: '',
        isValid: true,
      },
      email: {
        val: '',
        isValid: true,
      },
      phone: {
        val: null,
        isValid: true,
      },
      subject: {
        val: '',
        isValid: true,
      },
      description: {
        val: '',
        isValid: true,
      },
      budget: '',
      lunchDate: '',
      sentSucces: false,
    }
  },
  methods: {
    isValid() {
      this.formIsValid = true
      if (this.name.val === '') {
        this.name.isValid = false
        this.formIsValid = false
      } else {
        this.name.isValid = true
      }
      if (this.email.val === '' || !this.email.val.includes('@')) {
        this.email.isValid = false
        this.formIsValid = false
      } else {
        this.email.isValid = true
      }
      if (this.phone.val === null) {
        this.phone.isValid = false
        this.formIsValid = false
      } else {
        this.phone.isValid = true
      }
      if (this.subject.val === '') {
        this.subject.isValid = false
        this.formIsValid = false
      } else {
        this.subject.isValid = true
      }
      if (this.description.val === '') {
        this.description.isValid = false
        this.formIsValid = false
      } else {
        this.description.isValid = true
      }
    },
    clearData() {
      this.name.val = ''
      this.email.val = ''
      this.phone.val = null
      this.subject.val = ''
      this.description.val = ''

      this.name.isValid = true
      this.email.isValid = true
      this.phone.isValid = true
      this.subject.isValid = true
      this.description.isValid = true
    },
    sendForm() {
      this.isValid()
      if (this.formIsValid) {
        this.clearData()
        this.sentSucces = true
        setTimeout(() => (this.sentSucces = false), 5000)
      }
    },
  },
}
</script>
<style scoped lang="scss">
$main-color: #41b883;
$main-alt-color: #41b88337;

form {
  margin: 20px auto;
  width: 50%;
  border: 1px dotted $main-color;
  padding: 5px 10px;
  border-radius: 10px;

  @media (max-width: 767px) {
    width: 80%;
  }

  .control {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin: 10px auto;

    @media (max-width: 767px) {
      margin: 5px auto;
    }

    label {
      width: fit-content;
      margin-bottom: 10px;
      padding-left: 15px;

      @media (max-width: 767px) {
        margin-bottom: 6px;
        font-size: 14px;
      }
    }

    input {
      border: none;
      border-bottom: 1px solid $main-color;
      height: 40px;
      background-color: transparent;
      caret-color: black;
      outline: none;
      border-radius: 10px;
      caret-color: $main-color;
      padding-left: 15px;
      transition: 0.5s;

      @media (max-width: 767px) {
        height: 30px;
        font-size: 14px;
      }
    }

    .invalid {
      border: 1px solid red;
    }

    textarea {
      border: none;
      border-bottom: 1px solid $main-color;
      background-color: transparent;
      caret-color: $main-color;
      outline: none;
      border-radius: 10px;
      padding-left: 15px;
      transition: 0.5s;
    }

    select {
      border-radius: 10px;
      background-color: rgba(230, 222, 222, 0.461);
      border: none;
      border-bottom: 1px solid;
      padding-left: 15px;
      transition: 0.5s;

      option {
        background-color: rgba(230, 222, 222, 0.461);
        padding-left: 15px;
      }
    }

    input:hover,
    textarea:hover,
    select:hover {
      transform: scale(1.055) translateY(-4px);
    }

    button {
      width: 100%;
      border-radius: 10px;
      border: none;
      height: 40px;
      background-color: $main-alt-color;
      transition: 0.5s;
    }

    button:hover {
      transform: scale(0.85);
      background-color: $main-color;
      color: white;

    }

    p {
      color: red;
    }

    p {
      @media (max-width: 767px) {
        height: 15px;
        font-size: 14px;
      }
    }
  }

  .sent {
    text-transform: uppercase;
    color: green;
  }
}
</style>
